@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;500;700;800;900&display=swap);
.HomePage {
  text-align: center;
}

.nav {
    background-color: #a51f1f;
    height: 15vh;
    text-transform: uppercase;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 1;
    /* border-radius: 0 0 4rem 4rem; */
}

.esports-logo {
    top: 20vh;
    left: 0;
    right: 0;
}

.esports-logo img {
    height: 25vh;
}

.hero {
    display: flex;
    position: relative;
    align-items: center;
    justify-content: center;
    height: 85vh;
    width: 100%;
    overflow: hidden;
    margin-bottom: 6vh;
}

.hero #background-video {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    -webkit-filter: brightness(40%);
            filter: brightness(40%);
}

.hero-text h1 {
    margin: 0;
    color: #FFFFFF;
    text-transform: uppercase;
    font-size: 4vw;
    line-height: 4vw;
    -webkit-filter: drop-shadow(0 0.4rem 0 #a71f1f);
            filter: drop-shadow(0 0.4rem 0 #a71f1f);
}
.about-ourselves {
    padding-inline: 5vh;
    margin-bottom: 6vh;
}

.about-ourselves-desc {
    overflow: auto;
    display: flex;
    margin: auto;
    align-items: center;
    max-width: 60vw;
    justify-content: space-between;
    grid-column-gap: 5vw;
    -webkit-column-gap: 5vw;
            column-gap: 5vw;
    align-items: flex-start;
}

.about-ourselves h1 {
    text-transform: uppercase;
    font-size: 5vw;
    color: #a51f1f;
}

.about-ourselves h2 {
    text-transform: uppercase;
    line-height: 0;
    color: #a51f1f;
}

.community-image-grid {
    display: grid;
    line-height: 0;
    padding-bottom: 30px;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(2, 2fr);
}

.community-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.community-img--1 {
    grid-column-start: 1;
    grid-column-end: 3;
    grid-row-start: 1;
    grid-row-end: 3;
}

@media(min-width: 874px) {
    .about-ourselves h1 {
        text-transform: uppercase;
        font-size: 50px;
        color: #a51f1f;
    }
}
.meet-our-clubs {
    padding-inline: 5vh;
    margin-bottom: 6vh;
}

.meet-our-clubs h1 {
    text-transform: uppercase;
    font-size: 5vw;
    color: #a51f1f;
}

.clubs {
    display: flex;
}

.clubs::-webkit-scrollbar {
    height: 15px;
}

.clubs::-webkit-scrollbar-thumb {
    background-color: #a51f1f;
    border-radius: 10px;
}

.clubs::-webkit-scrollbar-thumb:hover {
    background-color: #851919;
}


.club {
    display: flex;
    position: relative;
    align-items: center;
    justify-content: center;
    background-size: auto 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-color: grey;
    background-blend-mode: multiply;
    width: 320px;
    height: 480px;
    border-radius: 20px;
    margin-left: 15px;
    margin-right: 15px;
    transition: width 0.3s;
}

.club::after {
    -webkit-filter: brightness(50%);
            filter: brightness(50%);
}

.club:hover {
    width: 370px;
}

#club-links {
    position: absolute;
    left: 0;
    right: 0;
    transition: opacity 0.3s;
    opacity: 0.5;
}

.club:hover #club-links {
    opacity: 1;
}

.club-contents h2 {
    text-transform: uppercase;
    color: white;
    width: 300px;
    font-size: 1.8rem;
    margin-bottom: 6px;
    -webkit-filter: drop-shadow(0 0.25rem 0 #a71f1f);
            filter: drop-shadow(0 0.25rem 0 #a71f1f);
}

.club-contents #social-media-icon {
    width: 1.8rem;
    color: white;
    -webkit-filter: invert(100%) drop-shadow(0 0.25rem 0 #a71f1f);
            filter: invert(100%) drop-shadow(0 0.25rem 0 #a71f1f);
}

@media(min-width: 874px) {
    .meet-our-clubs h1 {
        text-transform: uppercase;
        font-size: 50px;
        color: #a51f1f;
    }
}
a {
    text-decoration: none;
    color: #E8E8E8;
}

.footer {
    padding: 50px;
    color: white;
    text-align: left;
    background-color: #808080;
    max-width: 100%;
    list-style: none;
}

.footer h3 {
    line-height: 0;
}

.footer .footer-row {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around
}

.footer .footer-col {
    width: 35%;
}

.footer .footer-col ul {
    text-decoration: none;
    list-style-type: none;
    margin-bottom: 0;
    padding: 0;
    margin: 0;
}

.socials .social-icon {
    -webkit-filter: invert(100%);
            filter: invert(100%);
    width: 25px;
    padding: 5px;
}

.footer-end {
    padding: 10px;
    color: white;
    background-color: #606060;
}

@media(max-width: 874px) {
    .footer .footer-col {
        width: 100%;
    }
}
body {
  margin: 0;
  font-size: 100%;
  font-family: 'Inter', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1 {
  font-weight: 900;
}

h2 {
  font-weight: 800;
}

.Apply {
  text-align: center;
}

.job-openings {
    padding-left: 4vw;
    padding-right: 4vw;
    padding-top: 2vh;
    padding-bottom: 2vh;
}

.job-openings a {
    color: black;
    text-decoration: underline;
}

.job-openings h2 {
    line-height: 2px;
}

.job-openings-list {
    border-collapse:collapse;
    text-align: left;
    width: 100%;
}

.job-openings-list tr:nth-child(2n - 1) {
    background-color: #d3d3d3;
}

.job p {
    margin-left: 100px;
    margin-right: 100px;
    font-weight: normal;
}

th {
    height: 80px;
    border: 0 solid;
}

.info-button {
    display: inline-block;
    color: white;
    background-color: #a3031b;
    border-radius: 10px;
    padding: 13px 32px;
    text-align: center;
    text-decoration: none;
    transition: all .2s ease-in;
}

.info-button:hover {
    background-color: #750012;
}
.job-openings-header {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 700px;
    width: 100%;
    background: url(/static/media/job-openings.d256cf27.jpg) no-repeat;
    background-color: grey;
    background-blend-mode: multiply;
    background-size: 100% auto;
    background-position-y: center;
}

.job-openings-header h1 {
    color: white;
    font-size: 3.5rem;
}

